(function() {
  'use strict';

  angular
    .module('taro', [
      'ngAnimate',
      'ngAria',
      'ngRoute'
    ]);
})();

(function() {
  'use strict';

  angular
    .module('taro')
    .controller('MainController', ['$scope', function($scope){

    }]);
})();

(function(){
  'use strict';

  angular.module('taro')
    .controller('FullPagePanelController',['$scope', function($scope){
      $scope.dismiss = function(){
        $scope.dismissed = true;
      }
    }])
})();

(function(){
  'use strict';

  angular.module('taro')
    .directive('fullPagePanel',[function(){
      return {
        restrict: 'E',
        transclude: true,
        controller: 'FullPagePanelController',
        templateUrl: 'app/templates/panel.html'
      }
    }])
})();

(function() {
  'use strict';

  angular
    .module('taro')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/templates/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

angular.module("taro").run(["$templateCache", function($templateCache) {$templateCache.put("app/templates/main.html","<full-page-panel><h1>Estuardo Diaz</h1><h2>UX designer / Frontend Developer</h2><nav sticky-nav><ul><li><a href=\"\">My work</a></li><li><a href=\"\">About me</a></li><li><a href=\"\">Articles</a></li><li><a href=\"\">Experiments</a></li></ul></nav></full-page-panel>");
$templateCache.put("app/templates/panel.html","<main ng-class=\"{ dismissed: dismissed}\" ng-transclude></main><h1 id=panel-dismiss class=icon-down ng-click=dismiss()></h1><section><h1>Hi i still updating my website</h1><br><small>last updated 17/07/2016</small></section>");}]);